import { Blockchain, FaceSolanaNearTransactionPayload } from '@haechi-labs/face-types';
import { PublicKey, Transaction } from '@solana/web3.js';

import { Internal } from '../internal';

export class SolanaProvider {
  internal: Internal;

  constructor(internal: Internal) {
    this.internal = internal;
  }

  async getPublicKeys(): Promise<PublicKey[]> {
    const strPublicKeys = await this.internal.getAddresses(Blockchain.SOLANA);
    return strPublicKeys.map((strPublicKey) => {
      const strippedHex = strPublicKey.slice(2);
      const buffer = Buffer.from(strippedHex, 'hex');
      return new PublicKey(buffer);
    });
  }

  async signAndSendTransaction(transaction: Transaction): Promise<string> {
    const txPayload: FaceSolanaNearTransactionPayload = {
      serializedHex: transaction
        .serialize({
          requireAllSignatures: false,
          verifySignatures: false,
        })
        .toString('hex'),
    };
    const result = await this.internal.sendRpc<any>({
      method: 'solana_sendTransaction',
      params: [txPayload],
    });

    return result!;
  }
}
