import {
  Blockchain,
  Env,
  FaceLoginResponse,
  JsonRpcMethod,
  JsonRpcRequestPayload,
  JsonRpcResponsePayload,
  LoginProviderType,
  NetworkChainIdMap,
} from '@haechi-labs/face-types';
import { BigNumber, ethers } from 'ethers';

import { Network } from './face';
import { Iframe } from './iframe';

const DEFAULT_ETH_GAS_PRICE = BigNumber.from(100000).toHexString();

export class Internal {
  private readonly iframe: Iframe;

  constructor(apiKey: string, network?: Network, env?: Env, iframeUrl?: string) {
    const blockchain = this.getBlockchainFromNetwork(network);
    // TODO: env is not configured yet, so need to change as env configured
    const env1 = env ?? this.getDefaultEnv(network);
    this.iframe = new Iframe(apiKey, blockchain, env1, this.getIframeUrl(env1, iframeUrl));
  }

  async getAddresses(blockchain?: Blockchain): Promise<string[]> {
    const requestId = await this.iframe.sendChildMessage({
      method: JsonRpcMethod.face_accounts,
      params: [blockchain],
    });
    const response = await this.iframe.waitForResponse<null | string[]>(requestId);
    if (response) {
      return response;
    }
    return Promise.reject(new Error(`get address failed`));
  }

  async estimateGas(request: JsonRpcRequestPayload) {
    await Promise.all(
      request.params.map(
        async (param: { from: string; to: string; value: string; data?: string }) => {
          if (param.from) {
            if (param.data) {
              const abi = [
                'function transfer(address to, uint256 value) public returns (bool success)',
              ];
              const { name, args } = await this.decodeData(param.data, abi).catch(
                () => ({} as any)
              );

              if (args?.to && args?.value) {
                const balance = await this.getBalance(param.from, param.to);
                const newData = { to: args.to, value: args.value };

                if (balance.sub(newData.value).lt(0)) {
                  newData.value = '0x0';
                }
                param.data = await this.encodeData(abi, name, [newData.to, newData.value]);
              }
            } else {
              // platform coin
              const balance = await this.getBalance(param.from);

              if (balance.sub(param.value).lt(0)) {
                param.value = '0x0';
              }
            }
          }
        }
      )
    );

    return await this.sendRpc(request).catch(() => DEFAULT_ETH_GAS_PRICE);
  }

  async getBalance(address: string, contractAddress?: string): Promise<BigNumber> {
    if (contractAddress) {
      const callData = await this.encodeData(
        ['function balanceOf(address owner) view returns (uint256)'],
        'balanceOf',
        [address]
      );

      const result = await this.sendRpc({
        method: JsonRpcMethod.eth_call,
        params: [
          {
            to: contractAddress,
            data: callData,
          },
          'latest',
        ],
      });

      return BigNumber.from(result);
    }

    return BigNumber.from(
      await this.sendRpc({
        method: JsonRpcMethod.eth_getBalance,
        params: [address, 'latest'],
      })
    );
  }

  async ownerOf(contractAddress: string, tokenId: string): Promise<string> {
    const callData = await this.encodeData(
      ['function ownerOf(uint256 tokenId) view returns (address)'],
      'ownerOf',
      [tokenId]
    );
    const result = await this.sendRpc({
      method: JsonRpcMethod.eth_call,
      params: [
        {
          to: contractAddress,
          data: callData,
        },
        'latest',
      ],
    });

    return ('0x' + (result as string).substring(26)).toLowerCase();
  }

  async logout(): Promise<void> {
    const requestId = await this.iframe.sendChildMessage({
      method: JsonRpcMethod.face_logOut,
    });
    await this.iframe.waitForResponse<FaceLoginResponse | null>(requestId);
  }

  async getCurrentUser(): Promise<FaceLoginResponse | null> {
    const requestId = await this.iframe.sendChildMessage({
      method: JsonRpcMethod.face_currentUser,
    });
    return await this.iframe.waitForResponse<FaceLoginResponse>(requestId);
  }

  async isLoggedIn(): Promise<boolean> {
    await this.iframe.sendChildMessage({
      method: JsonRpcMethod.face_loggedIn,
    });
    const response = await this.iframe.waitForResponse<boolean>();
    if (response) {
      return response;
    }
    return false;
  }

  async ready(): Promise<void> {
    return this.iframe.ready();
  }

  async loginWithCredential(): Promise<FaceLoginResponse | null> {
    const requestId = await this.iframe.sendChildMessage({
      method: JsonRpcMethod.face_logInSignUp,
    });
    return await this.iframe.waitForResponse<FaceLoginResponse | null>(requestId);
  }

  async socialLogin(provider: LoginProviderType): Promise<FaceLoginResponse | null> {
    const requestId = await this.iframe.sendChildMessage({
      method: JsonRpcMethod.face_socialLogin,
      params: [provider],
    });
    return await this.iframe.waitForResponse<FaceLoginResponse | null>(requestId);
  }

  async openWalletConnect(name: string, url: string): Promise<void> {
    await this.iframe.sendChildMessage({
      method: JsonRpcMethod.face_openWalletConnect,
      params: [name, url],
    });
  }

  async sendRpc<T>(
    rpcPayload: JsonRpcRequestPayload
  ): Promise<JsonRpcResponsePayload<T>['result']> {
    const requestId = await this.iframe.sendChildMessage(rpcPayload);
    return await this.iframe.waitForResponse<JsonRpcResponsePayload<T>['result']>(requestId);
  }

  getBlockchainFromNetwork(network?: Network): Blockchain {
    switch (network) {
      case Network.ETHEREUM:
      case Network.GOERLI:
        return Blockchain.ETHEREUM;
      case Network.POLYGON:
      case Network.MUMBAI:
        return Blockchain.POLYGON;
      case Network.BNB_SMART_CHAIN:
      case Network.BNB_SMART_CHAIN_TESTNET:
        return Blockchain.BNB_SMART_CHAIN;
      case Network.KLAYTN:
      case Network.BAOBAB:
        return Blockchain.KLAYTN;
      case Network.SOLANA:
      case Network.SOLANA_DEVNET:
        return Blockchain.SOLANA;
      case Network.BORA:
      case Network.BORA_TESTNET:
        return Blockchain.BORA;
      case Network.NEAR:
      case Network.NEAR_TESTNET:
        return Blockchain.NEAR;
      default:
        return Blockchain.ETHEREUM;
    }
  }

  getIframeUrl(env?: Env, iframeUrl?: string): string {
    if (iframeUrl != null) {
      return iframeUrl;
    }
    switch (env) {
      case Env.Local:
        return 'http://localhost:3333';
      case Env.Dev:
        return 'https://app.dev.facewallet.xyz';
      case Env.StageTest:
        return 'https://app.stage-test.facewallet.xyz';
      case Env.ProdTest:
        return 'https://app.test.facewallet.xyz';
      case Env.StageMainnet:
        return 'https://app.stage.facewallet.xyz';
      case Env.ProdMainnet:
        return 'https://app.facewallet.xyz';
      default:
        return 'https://app.facewallet.xyz';
    }
  }

  getDefaultEnv(network?: Network): Env {
    if (!network) {
      return Env.ProdMainnet;
    }
    if (
      network === Network.GOERLI ||
      network === Network.MUMBAI ||
      network === Network.BAOBAB ||
      network === Network.BNB_SMART_CHAIN_TESTNET ||
      network === Network.BORA_TESTNET ||
      network === Network.SOLANA_DEVNET ||
      network === Network.NEAR_TESTNET
    ) {
      return Env.ProdTest;
    }
    return Env.ProdMainnet;
  }

  async decodeData(serializedTx: string, abi: string[]) {
    const ethersInterface = new ethers.utils.Interface(abi);
    const { name, args } = ethersInterface.parseTransaction({ data: serializedTx });

    return { name, args };
  }

  async encodeData(abi: string[], functionFragment: string, args?: any[]) {
    const ethersInterface = new ethers.utils.Interface(abi);
    return ethersInterface.encodeFunctionData(functionFragment, args);
  }

  async switchEthereumChain(request: JsonRpcRequestPayload) {
    const blockchain = this.getBlockchainFromNetwork(
      NetworkChainIdMap[Number(request.params[0].chainId)] as Network
    );
    request.params[0].blockchain = blockchain;
    const res = await this.sendRpc(request);

    this.iframe.setBlockchain(blockchain);

    return res;
  }
}
