import { FaceLoginResponse, LoginProviderType } from '@haechi-labs/face-types';

import { Internal } from './internal';

export class Auth {
  private readonly internal: Internal;

  constructor(internal: Internal) {
    this.internal = internal;
  }

  async login(): Promise<FaceLoginResponse | null> {
    return this.internal.loginWithCredential();
  }

  async socialLogin(provider: LoginProviderType): Promise<FaceLoginResponse | null> {
    return this.internal.socialLogin(provider);
  }

  async logout(): Promise<void> {
    await this.internal.logout();
  }

  async getCurrentUser(): Promise<FaceLoginResponse | null> {
    return await this.internal.getCurrentUser();
  }

  async isLoggedIn(): Promise<boolean> {
    return await this.internal.isLoggedIn();
  }
}
