export enum JsonRpcMethod {
  face_logInSignUp = 'face_logInSignUp',
  face_loggedIn = 'face_loggedIn',
  face_logOut = 'face_logOut',
  face_currentUser = 'face_currentUser',
  face_accounts = 'face_accounts',
  face_openIframe = 'face_openIframe',
  face_closeIframe = 'face_closeIframe',
  face_ready = 'face_ready',
  face_openWalletConnect = 'face_openWalletConnect',
  face_socialLogin = 'face_socialLogin',

  eth_sendTransaction = 'eth_sendTransaction',
  eth_getBalance = 'eth_getBalance',
  eth_call = 'eth_call',
  eth_estimateGas = 'eth_estimateGas',
  eth_sign = 'eth_sign',
  eth_accounts = 'eth_accounts',

  personal_sign = 'personal_sign',
  personal_listAccounts = 'personal_listAccounts',

  wallet_switchEthereumChain = 'wallet_switchEthereumChain',

  solana_sendTransaction = 'solana_sendTransaction',

  near_broadcast_tx_async = 'near_broadcast_tx_async',
}

export enum JsonRpcSource {
  FACE_SDK = 'FACE_SDK',
  FACE_IFRAME = 'FACE_IFRAME',
  FACE_WALLET_CONNECT = 'FACE_WALLET_CONNECT',
  FACE_NATIVE_SDK = 'FACE_NATIVE_SDK',
}

export interface JsonRpcRequestPayload<TParams = any> {
  id?: string | number | null;
  method: JsonRpcMethod | string;
  params?: TParams;
  jsonrpc?: string;
  userId?: string;
  blockchain?: string;
  to?: JsonRpcSource;
  from?: JsonRpcSource;
}

export interface JsonRpcRequestCallback {
  /** Callback executed upon JSON RPC response. */
  (err: JsonRpcError | null, result?: JsonRpcResponsePayload | null): void;
}

export interface JsonRpcBatchRequestCallback {
  /** Callback executed upon JSON RPC response. */
  (err: JsonRpcError | null, result?: (JsonRpcResponsePayload | null)[] | null): void;
}

// --- Response interfaces

export interface JsonRpcError {
  message: string;
  code?: number;
  data?: any;
}

export interface JsonRpcResponsePayload<ResultType = any> {
  jsonrpc?: string;
  id?: string | number | null;
  result?: ResultType | null;
  error?: JsonRpcError | null;
  // Originally, we were going to work with JSONRPC standard,
  // and wrap ethereum rpc in one more layer.
  // However, the process of handling data became complicated,
  // so we decided to extend JSONRPC standard.
  // Link for internal discussion:
  // https://haechilabs.slack.com/archives/C03EVTP0RSB/p1666330252362169?thread_ts=1666329876.458129&cid=C03EVTP0RSB
  to?: JsonRpcSource;
  from?: JsonRpcSource;
}

export enum RpcErrorCode {
  // Standard JSON RPC 2.0 Error Codes
  PARSE_ERROR = -32700,
  INVALID_REQUEST = -32600,
  METHOD_NOT_FOUND = -32601,
  INVALID_PARAMS = -32602,
  INTERNAL_ERROR = -32603,
}
